import { FileOutlined } from "@ant-design/icons";
import { CInput, CTextArea } from "@components";
import { Button, Col, Form, Row } from "antd";

const CEditAssetAttachments = ({
  onPickFile1,
  onPickFile2,
  onPickFile3,
  forms,
  handleChange,
  onDescriptionSatu,
  onDescriptionDua,
  onDescriptionTiga,
  fileName1,
  fileName2,
  fileName3,
  handlePickFileSatu,
  handlePickFileDua,
  handlePickFileTiga,
}) => {
  const layout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 4 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 20 },
    },
  };

  return (
    <Row gutter={20}>
      <Col span={8}>
        <Button
          icon={<FileOutlined />}
          style={{ width: "100%", marginBottom: 35 }}
          type="primary"
          onClick={() => handlePickFileSatu()}
        >
          {fileName1}
        </Button>
        <input
          className="form-control form-control-sm"
          id="attachments-1"
          type="file"
          onChange={(e) => onPickFile1(e.target.files[0])}
          style={{ display: "none" }}
        />
        <br />

        <Form {...layout}>
          <CInput
            label="Name"
            style={{ marginTop: -25 }}
            placeholder="Name"
            id="file_name_satu"
            value={forms.file_name_satu}
            onChange={(e) => handleChange(e)}
          />
          <CTextArea
            label="Description"
            placeholder="Description"
            id="file_description_satu"
            value={forms.file_description_satu}
            onChange={onDescriptionSatu}
            style={{ marginTop: -10 }}
          />
        </Form>
      </Col>
      <Col span={8}>
        <Button
          icon={<FileOutlined />}
          style={{ width: "100%", marginBottom: 35 }}
          type="primary"
          onClick={() => handlePickFileDua()}
        >
          {fileName2}
        </Button>
        <input
          className="form-control form-control-sm"
          id="attachments-2"
          type="file"
          onChange={(e) => onPickFile2(e.target.files[0])}
          style={{ display: "none" }}
        />
        <br />

        <Form {...layout}>
          <CInput
            label="Name"
            style={{ marginTop: -25 }}
            placeholder="Name"
            id="file_name_dua"
            value={forms.file_name_dua}
            onChange={(e) => handleChange(e)}
          />
          <CTextArea
            label="Description"
            placeholder="Description"
            id="file_description_dua"
            value={forms.file_description_dua}
            onChange={onDescriptionDua}
            style={{ marginTop: -10 }}
          />
        </Form>
      </Col>
      <Col span={8}>
        <Button
          icon={<FileOutlined />}
          style={{ width: "100%", marginBottom: 35 }}
          type="primary"
          onClick={() => handlePickFileTiga()}
        >
          {fileName3}
        </Button>
        <input
          className="form-control form-control-sm"
          id="attachments-3"
          type="file"
          onChange={(e) => onPickFile3(e.target.files[0])}
          style={{ display: "none" }}
        />
        <br />
        <Form {...layout}>
          <CInput
            label="Name"
            style={{ marginTop: -25 }}
            placeholder="Name"
            id="file_name_tiga"
            value={forms.file_name_tiga}
            onChange={(e) => handleChange(e)}
          />
          <CTextArea
            label="Description"
            placeholder="Description"
            id="file_description_tiga"
            value={forms.file_description_tiga}
            onChange={onDescriptionTiga}
            style={{ marginTop: -10 }}
          />
        </Form>
      </Col>
    </Row>
  );
};

export default CEditAssetAttachments;
